import QRCode from 'qrcode'
import './code.less'

let page = document.getElementById('page'), 
    cli = document.getElementById('client'),
    new_class = page.className,
    qr_ele = document.getElementById('qr'),
    load = document.getElementById('load'),
    detail = document.getElementById('detail'),
    txcurrcd = document.getElementById('txcurrcd'),
    txamt = document.getElementById('txamt')
let params = location.search.slice(1)
let params_arr = params.split('&')
let params_obj = {}
params_arr.forEach(param => {
    let arr = param.split('=')
    let key = arr[0]
    let val = arr[1]
    params_obj[key] = val
})
let data_type = ''
if(params_obj.p1 && params_obj.p2){
    data_type = params_obj.p1 + params_obj.p2
    try{
        data_type = decodeURIComponent(data_type)
    }catch(e){
        data_type = ''
    }
}
let type = '800101' //默认支付宝支付
switch(data_type){
    case '支付宝支付':
        type = '800101'
        cli.innerHTML = '支付宝'
        break
    case '微信支付':
        type = '800201'
        cli.innerHTML = '微信'
        break
    default:
        type = '800101'
        cli.innerHTML = '支付宝'
}
axios.get('http://qpay.mengant.cn/api/v1/order/info')
    .then(res => {
        let data = res.data
        txcurrcd.innerHTML = data.txcurrcd
        txamt.innerHTML = data.txamt
        detail.style.display = 'block'
    })
axios.get('http://qpay.mengant.cn/api/v1/pay/qcode', {
    params: {
        type: type
    }
}).then(res => {
    let qrcode = res.data.qrcode.replace('\/', '/')
    QRCode.toDataURL(res.data.qrcode)
      .then(url => {
        load.style.display = 'none'
        qr.src = url
      })
})

